<!-- 手机Loading -->
<template>
  <div class="loading-wrapper-phone">
    <div class="toast toast-loading">
      <img class="closeModal" @click="closeModal" :src="close_icon" alt="关闭按钮">
      <img  class="market" :src="img_src" alt="商城暂未开通">
      <div class="modalText">
        <span>{{title}}</span>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    props: [
      'title'
    ],
    data() {
      return {
        img_src: require('../assets/img/tj_bg.png'),
        close_icon: require('../assets/img/guanbi.png'),
      }
    },
    methods: {
      closeModal(){
        this.$emit('closeMarketModal',0);
      }
    }
  }
</script>
<style lang="less" scoped>
  .loading-wrapper-phone {
    position: fixed;
    z-index: 1000;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, .5);
    .toast {
      left:50%;
      top: 50%;
      transition-duration: .2s;
      z-index: 100;
      position: fixed;
      min-height: 500px;
      border-radius: 5px;
      color: #fff;
      transform: translate(-50%,-50%);
      &>.market{
        max-width: 500px;
      }
      &>.closeModal{
        position: absolute;
        top: 13%;
        right: 0;
        width: 10%;
      }
      &>.modalText{
        position: absolute;
        bottom: 20px;
        left: 50%;
        transform: translate(-50%,-50%);
        text-align: center;
        font-size: 30px;
        &>span{
          color: #000;
        }
      }
    }
  }
</style>


