<style lang="less" scoped>
  body {
    background-color: #eee;
  } // 发布作品 顶部NAV START
  header {
    display: -webkit-flex;
    display: flex;
    background-color: #fff;
    height: 90px;
  }
  header * {
    -webkit-box-flex: 1;
    flex: 1;
    text-align: center;
    line-height: 90px;
  }
  header *:nth-child(1) {
    color: #aaa;
    font-size: 25px;
  }
  header *:nth-child(2) {
    font-size: 30px;
  }
  header *:nth-child(3) {
    color: #39bd6c;
    font-size: 25px;
  } 
  // 发布作品 顶部NAV END
  section {
    width: 100%;
    height: 100%;
    background-color: #fff;
    padding: 0 0 20px 0;
    .textarea_content {
      width: 100%;
      text-align: center;
    }
    .textarea_content>textarea {
      width: 93%;
      height: 400px;
      resize: none;
      border: none;
      outline: none;
      font-size: 25px;
      padding-top: 30px;
      &::-ms-input-placeholder{
        color: #c5c5c5;
      }
      &::-webkit-input-placeholder{
        color: #c5c5c5;
      }
      &::-o-input-placeholder{
        color: #c5c5c5;
      }
      &::-moz-input-placeholder{
        color: #c5c5c5;
      }
    }
    .textarea_content>textarea::-webkit-input-placeholder,
    textarea::-moz-placeholder {
      font-size: 30px;
    }
    .font_num {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 20px;
      &>span{
        flex: 1;
        height: 60px;
        &>b{
          display: inline-block;
          width: 200px;
          height: 50px;
          line-height: 50px;
          text-align: center;
          border-radius: 0 30px 30px 30px;
          font-weight: normal;
          font-size: 30px;
        }
        &:nth-child(1){
          padding-left: 30px;
        }
        &:nth-child(2){
          color: #aaa;
          text-align: right;
          padding-right: 30px;
          font-size: 30px;
          &>i{
            font-size: 30px;
            font-style: normal;
          }
          &>i.default{
            color: #c5c5c5;
          }
          &>i.blackColor{
            color: #292929;
          }
        } 
      }
    }
    .upload_file {
      padding: 0 30px;
      .imgs_uploaded {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        flex-wrap: wrap;
        &>div{
          width: 32%;
        }
        &>span {
          display: inline-block;
          width: 6.25rem;
          height: 6.25rem;
          position: relative;
          &>img {
            width: 100%;
            height: 100%;
            box-sizing: border-box;
            padding: .3125rem;
          }
          &>span {
            display: inline-block;
            width: .9375rem;
            height: .9375rem;
            background-color: red;
            color: #fff;
            text-align: center;
            line-height: .9375rem;
            border-radius: 50%;
            position: absolute;
            top: .625rem;
            right: .625rem;
          }
        }
        &>.file_container {
          text-align: left;
          box-sizing: border-box;
          position: relative;
          display: inline-block;
          width: 164px;
          height: 164px;
          .file_deleteicon {
            position: absolute;
            top: 0;
            right: 0;
            width: 30px;
            height: 30px;
            border-radius: 50%;
            background-color: #f00;
            color: #fff;
            text-align: center;
            line-height:30px;
            font-size: 30px;
          }
          .file {
            position: absolute;
            top: 0;
            left: 0;
            width: 164px;
            height: 164px;
            opacity: 0;
            filter: opacity(0);
            overflow: hidden;
          }
          img {
            width: 164px;
            height: 164px;
          }
        }
      }
    }
  } // 课程标题
  .task_title {
    width: 100%;
    line-height: 70px;
    font-size: 36px;
    overflow-y: auto;
    overflow-x: hidden;
    padding-left: 30px;
    box-sizing: border-box;
    color: #292929;
    font-weight: bold;
    padding-right: 30px;
  }
  .designSchool>b{
    color: #1c79ed;
    background-color: rgb(169, 228, 255);
  }
  .artSchool>b{
    color: #ed741c;
    background-color: #ecc7a4;
  }
  .infomation{
    color: #9e9e9e;
    font-size: 24px;
    padding: 0 30px;
    margin-bottom: 30px;
  }
  .submitWorkBtn{
    text-align: center;
    position: fixed;
    bottom: 100px;
    left: 50%;
    transform: translateX(-50%);
    z-index: 3;
    display: inline-block;
    width: 550px;
    height: 88px;
    border: none;
    outline: none;
    color: #fff;
    font-size: 30px;
    margin: 20px 0;
    border-radius: 50px;
    background-color: rgba(78, 224, 72, 0.8);
  }
</style>

<template>
  <div style="min-height: 100vh;background-color: #fff;">
    <!-- 设置返回按钮 START-->
    <headerNavBack centerTitle="提交作业"></headerNavBack>

    <!-- 设置返回按钮 END -->
    <section>
      <div class="task_title" v-html="taskInfo.content"></div>
      <div class="textarea_content">
        <textarea name="work_content" maxlength="150" id="work_content" placeholder="简单阐述一下你的想法" v-model="content"></textarea>
      </div>
      <div v-if="taskInfo.course.categoryId === 'j5m484vz'" class="font_num"><span class="designSchool"><b>#设计学院</b></span><span><i class="default" v-if="content.length === 0">0</i><i class="default" v-if="content.length > 0">{{150 - content.length > 0 ? content.length : 150}}</i>/150</span></div>
      <div v-if="taskInfo.course.categoryId === 'j5m48deg'" class="font_num"><span class="artSchool"><b>#美术学院</b></span><span><i class="default"></i><i class="blackColor" v-if="content.length > 0">{{150 - content.length > 0 ? content.length : 150}}</i>/150</span></div>
      <div class="infomation">注: 单张图片上传限制2M, 9张上传限制9M, 手机拍摄单张照片上传限制5M</div>
      <div class="upload_file">
        <div class="imgs_uploaded">
          <span v-for="(img, index) in imgList" :key="index">
              <img :src="img" alt="上传图片">
          </span>
          <div class="file_container" v-if="img_arr && img_arr.length <9">
            <input type="file" accept="image/*" id="file" class="file" name="file" @change="changeImg($event)" ref="file">
            <img src="../../../static/images/plus_upload.png" alt="自定义上传组件">
          </div>
          <div class="file_container" v-for="(imgItem, index) in img_arr" :key="index">
            <span class="file_deleteicon" @click="deleteicon(index)">×</span>
            <img :src="imgItem" alt="作业图片">
          </div>
        </div>
      </div>
    </section>
    <button @click="publishWork" class="submitWorkBtn">提交作业</button>
    <transition enter-active-class="zoomIn" leave-active-class="zoomOut">
      <get-intertal class="animated" title="恭喜获得1个积分" v-if="showGetIntertal" v-on:closeMarketModal="closeMarketModal"></get-intertal>
    </transition>
  </div>
</template>
<script>
  import headerNavBack from '@/components/headerNavBack'
  import getIntertal from '@/components/getIntertal'
  export default {
    components: {
      headerNavBack,
      getIntertal
    },
    data() {
      return {
        params: {
          content: '',
          filename: null,
        },
        base64: [],
        base: new Array(),
        imgList: [],
        content: '',
        jobInfo: {
          imgList: []
        },
        base64_container: [],
        title: '',
        // 任务详细信息
        taskInfo: {
          course: {
            categoryId: ''
          }
        },
        img_arr: [],
        // 图片对象数组
        imgInfo: [],
        // 是否显示积分弹出层
        fixFile: ['png', 'gif', 'jpeg', 'jpg'],
        // 总大小
        totelCore: 0,
        fileTotalSize: 1024 * 1024 * 9,
        showGetIntertal: 0
      }
    },
    methods: {
      // 改变积分弹出层的显示状态
      closeMarketModal(info){
        this.showGetIntertal = info
        this.$store.state.getMyWorkDataListPage = 1
        this.$store.state.getMyWorkDataList = []
        this.$router.push('/workArea/all/myWorkInRange')
        this.$store.commit('cleanWorkRange',this.$route.fullPath)
      },
      // 保存作业
      publishWork() {
        //清空数据
        this.$store.commit('clearList')

        if (this.content.length == 0 || this.content.charAt(0) == ' ') {
          this.$Toast({
            message: '请阐述一下你的想法',
            position: 'middle',
            duration: 1000
          })
          return
        }

        this.$Indicator.open({
          text: '保存作业中...',
          spinnerType: 'fading-circle'
        })
        
        // 计算总大小
        if(this.imgInfo.length > 0){
          this.imgInfo.forEach(element => {
            this.totelCore += element.size
          })
        }else{
          this.$Toast({
            message: '请上传作业',
            position: 'middle',
            duration: 1000
          })
          return
        }

        // 限制总大小
        if(this.fileTotalSize < this.totelCore){
          this.$Toast({
            message: '单张图片上传限制2M, 9张上传限制9M, 手机拍摄单张照片上传限制5M',
            position: 'middle',
            duration: 1000
          })
          return
        }

        this.axios.post('/api/user/saveJob', {
          taskId: this.$route.params.taskId,
          content: this.content,
          imgInfo: JSON.stringify(this.imgInfo)
        }).then(data => {
          if(data.data.status === 'failure'){
            this.$Indicator.close()
            this.$Toast({
              message: data.data.result,
              position: 'middle',
              duration: 1000
            })
            return
          }
          this.$Indicator.close()
          this.$Toast({
            message: '提交作业成功',
            position: 'middle',
            duration: 1000
          })
          // 修改作业获得0积分
          if(!data.data.result.integal){
            setTimeout(() => {
              this.$router.push('/workArea/all/myWorkInRange')
              this.$store.commit('cleanWorkRange',this.$route.fullPath)
            }, 1000)
          }else{
            // 提交作业获得1积分, 显示弹出层
            this.showGetIntertal = 1
          }
        }).catch(err => {
          console.log(err)
        });
      },
      // 添加图片,追加到数组容器, 判断上传的图片格式
      changeImg($event) {
        if(this.fixFile.join('').indexOf($event.target.files[0].type.substring($event.target.files[0].type.indexOf('/') + 1, $event.target.files[0].type.length)) === -1){
          this.$Toast({
            message: '请上传JPG, PNG, GIF格式的作业',
            position: 'middle',
            duration: 1000
          })
          return
        }

        if($event.target.files[0].size > 2 * 1024 * 1024){
          this.$Toast({
            message: '上传图片超过2M限制, 请重新选择上传',
            position: 'middle',
            duration: 1000
          })
          return
        }

        this.$Indicator.open({
          text: '上传中...',
          spinnerType: 'fading-circle'
        })

        if ($event.target.files.length > 0) {
          this.base64.push($event.target.files)
          const img_url = window.URL ? window.URL.createObjectURL($event.target.files[0]) : window.URL.createObjectURL($event.target.files[0])
          this.jobInfo.imgList.unshift(img_url)
          var reader = new FileReader()
          reader.readAsDataURL($event.target.files[0])
          reader.onload = (event) => {
            this.axios.post('/api/upload_file', {
              content: event.target.result
            }).then((data) => {
              this.img_arr.unshift(data.data.data[0])
              let exp = $event.target.files[0].type.replace(/image\//,'')
              this.imgInfo.unshift({
                filename:`${$event.target.files[0].name}.${exp}`,
                url: `${data.data.data[0]}`,
                size: $event.target.files[0].size
              })
              this.$Indicator.close()
            })
          }
        }
      },
      deleteicon(index) {
        this.img_arr.forEach((element, i) => {
          if (i == index) {
            this.img_arr.splice(index, 1)
          }
        })
        this.imgInfo.forEach((element, i) => {
          if (i == index) {
            this.imgInfo.splice(index, 1)
          }
        })
      },
    },
    mounted() {
      window.onscroll = () => {
        return
      }
      this.$Indicator.open({
        text: '加载中...',
        spinnerType: 'fading-circle'
      })
      this.axios.post('/api/taskDetail', {
        taskId: this.$route.params.taskId
      }).then(data => {
        this.taskInfo = data.data
        this.axios.post('/api/user/taskJobList', {
          taskId: data.data.taskId
        }).then(data => {
          if (data.data.list[0]) {
            this.jobInfo = data.data.list[0]
            this.content = this.jobInfo.jobcompletionE.content
            this.jobInfo['imgList'] = this.jobInfo.jobcompletionE.contentImg.split(',')
            this.jobInfo.imgList.forEach(element => {
              this.img_arr.push(element)
            })
            this.imgInfo = JSON.parse(this.jobInfo.jobcompletionE.imgInfo)
            this.$Indicator.close()
          } else {
            this.$Indicator.close()
          }
        }).catch(err => {
          console.log(err)
        });
      }).catch(err => {
        console.log(err)
      });
    }
  }
</script>



